import { SharedModule } from './shared.module';
import { NgModule, ErrorHandler } from '@angular/core';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { IonicStorageModule } from "@ionic/storage"; //<-add this
import { SuperTabsModule } from 'ionic2-super-tabs';
import { MODULES, PROVIDERS } from './app.imports';
@NgModule({
  declarations: [
    // App Core
    MyApp,
  ],
  imports: [
    MODULES,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/card-social/card-social.module.ngfactory#CardSocialPageModuleNgFactory', name: 'CardSocialPage', segment: 'card-social', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/chat/chat.module.ngfactory#ChatModuleNgFactory', name: 'Chat', segment: 'chat', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/groupbuddies/groupbuddies.module.ngfactory#GroupbuddiesPageModuleNgFactory', name: 'GroupbuddiesPage', segment: 'groupbuddies', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/groupinfo/groupinfo.module.ngfactory#GroupinfoPageModuleNgFactory', name: 'GroupinfoPage', segment: 'groupinfo', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/grouptransaction/grouptransaction.module.ngfactory#ModuleNgFactory', name: 'GrouptransactionPage', segment: 'grouptransaction', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/groupmembers/groupmembers.module.ngfactory#GroupmembersPageModuleNgFactory', name: 'GroupmembersPage', segment: 'groupmembers', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ionic-tabs-page-1/ionic-tabs-page-1.module.ngfactory#IonicTabsPage_1PageModuleNgFactory', name: 'IonicTabsPage_1Page', segment: 'ionic-tabs-page-1', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#ModuleNgFactory', name: 'HomePage', segment: 'home/:type', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ionic-tabs/ionic-tabs.module.ngfactory#IonicTabsPageModuleNgFactory', name: 'IonicTabsPage', segment: 'ionic-tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ionic-tabs-page-2/ionic-tabs-page-2.module.ngfactory#IonicTabsPage_2PageModuleNgFactory', name: 'IonicTabsPage_2Page', segment: 'ionic-tabs-page-2', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/page-to-push/page-to-push.module.ngfactory#ModuleNgFactory', name: 'PageToPushPage', segment: 'page-to-push', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/page2/page2.module.ngfactory#Page2PageModuleNgFactory', name: 'Page2Page', segment: 'page2', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/page1/page1.module.ngfactory#ModuleNgFactory', name: 'Page1Page', segment: 'page1', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/page3/page3.module.ngfactory#ModuleNgFactory', name: 'Page3Page', segment: 'page3', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/partial-home/partial-home.module.ngfactory#ModuleNgFactory', name: 'PartialHomePage', segment: 'partial-home/:type', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/referral/referral.module.ngfactory#ReferralPageModuleNgFactory', name: 'ReferralPage', segment: 'referral', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/popup-modal/popup-modal.module.ngfactory#PopupModalPageModuleNgFactory', name: 'PopupModalPage', segment: 'popup-modal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/reports/reports.module.ngfactory#ReportsPageModuleNgFactory', name: 'ReportsPage', segment: 'reports', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signup/signup.module.ngfactory#SignupPageModuleNgFactory', name: 'SignupPage', segment: 'signup', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signupotp/signupotp.module.ngfactory#SignupotpPageModuleNgFactory', name: 'SignupotpPage', segment: 'signupotp', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/slide-walkthrough/slide-walkthrough.module.ngfactory#SlideWalkthroughPageModuleNgFactory', name: 'SlideWalkthroughPage', segment: 'slide-walkthrough', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot({
      name: '__yourappname',
      driverOrder: ['indexeddb', 'sqlite', 'websql']
    }), //<-add this
    SharedModule,
    SuperTabsModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [PROVIDERS, { provide: ErrorHandler, useClass: IonicErrorHandler }]
})
export class AppModule { }



